              <h2>Разработка Фирменного Стиля</h2>
              <p>Создайте незабываемое первое впечатление, с помощью потрясающего визуального стиля вашего бренда!</p>
              <div class="buttons">
                <router-link :to="{ path: briefLink }" class="button" @click.native="scrollToTop()">Заполнить бриф</router-link>
                <button class="button button-transparent" @click="scrollToPrices ()">Цены</button>
              </div>
            </div>
            <div class="col-12 col-lg-7 d-flex justify-content-center justify-content-lg-end">
              <div class="img-container">
                <picture>
                  <source srcset="img/static/Identify/1.webp">
                  <img srcset="img/static/Identify/1.png" alt="">
                </picture>
              </div>
            </div>
          </div>
        </div>
      </section>
      <section class="info">
        <div class="container">
          <div class="row">
            <h3>Что такое Фирменный стиль и что в него входит:</h3>
            <div class="col-12 col-lg-10">
              <p>В конечном итоге фирменный стиль - это способ общаться с миром, выделяться среди конкурентов и создавать впечатление о бренде, которое побуждает людей взаимодействовать с вами. Некоторые бренды превращают айдентику в искусство (например, Apple, LEGO или Levi’s).</p>
            </div>
          </div>
        </div>
      </section>
      <section class="info-list">
        <div class="container">
          <div class="row">
            <h3>Что чаще всего входит в Фирменный стиль?</h3>
            <div class="col-12 col-lg-12">
              <p>При разработке айдентики вам необходимо создать всеобъемлющий визуальный язык, который можно применить ко всему, от вашего веб-сайта до упаковки. И зависимости от ваших целей или продукта, этот список может быть больше или меньше:</p>
            </div>
            <ul class="col-12 col-lg-8">
              <li>Логотип</li>
              <li>Цветовая палитра</li>
              <li>Типография</li>
              <li>Печатная продукция</li>
              <li>Фотографии</li>
              <li>Иллюстрации</li>
              <li>Иконки</li>
              <li>Креативы</li>
              <li>Интерактивные элементы</li>
              <li>Веб-дизайн</li>
              <li>Брендбук</li>
            </ul>
          </div>
        </div>
      </section>
      <ProcessDev />
      <SectionPortfolio
          v-bind:portfolio = 'portfolio'
      />
      <Prices
          v-bind:priceProducts = 'priceProducts'
      />
      <OurFeatures class="features" />
    </main>
    <MailForm />
