</template>

<script>
import MailForm from '@/components/MailForm'
import ProcessDev from '../components/ProcessDev'
import SectionPortfolio from '../components/SectionPortfolio'
import OurFeatures from '../components/OurFeatures'
import Prices from '../components/Prices'
export default {
  components: { MailForm, ProcessDev, SectionPortfolio, Prices, OurFeatures },
  props: ['portfolio'],
  data () {
    return {
      priceProducts: [
        { name: 'Стартап', price: '10 000', feature1: 'Логотип', feature2: 'Цветовая схема', feature3: 'Типография', feature4: 'Визитка' },
        { name: 'Компания', price: '30 000', feature1: 'Услуги пакета "Стартап"', feature2: 'Паттерн / формы', feature3: 'Иконки', feature4: 'Иллюстрация' },
        { name: 'Корпорация', price: '100 000', feature1: 'Услуги пакета "Компания"', feature2: 'Маскот / Персонаж', feature3: 'Изображения (подбор стиля)', feature4: 'Брендбук' }
      ]
    }
  },
  computed: {
    briefLink () {
      return this.$store.getters.briefLink
    }
  },
  methods: {
    scrollToTop() {
      window.scrollTo(0,0);
    },
    scrollToPrices () {
      let headerHeight = document.querySelector('.header').offsetHeight,
          pricesPosition = this.$el.querySelector('.prices').getBoundingClientRect().top - headerHeight,
          scrollOptions = {
            left: 0,
            top: pricesPosition,
            behavior: 'smooth'
          }
      window.scrollTo( scrollOptions)
    }
  },
  mounted() {
    this.$store.dispatch('formTopPosition')
  }
}
</script>

